<template>
	<v-app id="app">
		<div id="kc" class="myTable">
			<b-form-group label="*Lote"
						  label-for="id_cultivo"
						  @submit.stop.prevent="handleSubmit">
				<v-autocomplete id="id_cultivo"
								v-model="cultivousado"
								@change="testFetch()"
								:items="opcultivos"
								placeholder="Elija el cultivo"
								required
								solo
								filled>
				</v-autocomplete>
			</b-form-group>
			<vue-headful title="Kc - Water"></vue-headful>
			<h2>Listado de Kc</h2>
			<b-button @click="testFetch"
					  class="mb-4"
					  title="Recargar"
					  variant="light">
				<v-icon dark style="color: black">mdi-cached</v-icon>
				Actualizar
			</b-button>
			<b-button class="mb-4 ml-2"
					  id="btn_del_full"
					  title="Importar desde Excel"
					  v-if="dardealta == false"
					  style="background-color: #00919F;color:white;"
					  v-b-modal.modal-excel>
				Importar Excel
			</b-button>
			<b-modal no-close-on-backdrop
					 ref="my-modal"
					 id="modal-excel"
					 hide-footer
					 title="Importar desde Excel"
					 ok-only>
				<h4>Elige un archivo para importar</h4>
				<p>Recuerda que un link para id_cultivo se forma de la siguiente manera:</p>
				<b>http://45.162.168.170:8081/cultivos/ + id_cultivo + / (puedes encontrarlo en el listado de cultivos)</b>
				<br />
				<br />
				<input type="file" @change="uploadFile" ref="archivo" accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel" />
				<b-button class="mt-2" block @click="hideModal" title="Volver Atras">
					Volver Atras
				</b-button>

				<b-button class="mt-3"
						  variant="success"
						  block
						  title="Importar"
						  @click="importarExcel">
					Importar
				</b-button>
			</b-modal>
			<!-- ================ALTA GASTO SALIENTE======================== -->
			<b-button class="mb-4 ml-2"
					  v-b-modal.modal-alta
					  @click="altaKc()"
					  title="Nuevo Kc"
					  v-if="dardealta == false"
					  style="background-color: #00919F;color:white;">
				<v-icon dark> mdi-plus </v-icon>
				Nuevo Kc
			</b-button>
			<b-modal id="modal-alta" hide-footer>
				<template #modal-title>
					<h5 class="modal-title">Alta</h5>
				</template>
				<kc-alta :updateTable="testFetch" />
			</b-modal>

			<!-- ================ELIMINAR VARIOS GASTOS======================== -->
			<div>
				<b-modal ref="my-modal"
						 id="modal-eliminarTodo"
						 hide-footer
						 title="Eliminar"
						 ok-only>
					<div class="d-block text-center" v-if="selected.length === rows">
						<h3>¿Esta seguro de eliminar todos los registros ?</h3>
					</div>
					<div class="d-block text-center" v-else>
						<h3>¿Esta seguro de eliminar {{ selected.length }} registros ?</h3>
					</div>

					<b-button class="mt-2" block @click="hideModal" title="Volver Atras">
						Volver Atras
					</b-button>

					<b-button class="mt-3"
							  variant="danger"
							  block
							  title="Eliminar"
							  @click="delete_all_Kc()">
						Eliminar
					</b-button>
				</b-modal>
			</div>
			<!-- ======================================== -->
			<!-- ======== Formulario de Busqueda ======== -->
			<b-form-group label-for="filter-input"
						  label-align-sm="right"
						  label-size="sm"
						  class="mb-0"
						  style="width: 100%; padding-bottom: 1em"
						  v-show="rows > 0">
				<b-input-group size="sm">
					<b-form-input id="filter-input"
								  v-model="filter"
								  type="search"
								  placeholder="Buscar registros"></b-form-input>

					<b-input-group-append>
						<b-button :disabled="!filter" @click="filter = ''">Limpiar</b-button>
					</b-input-group-append>
				</b-input-group>
			</b-form-group>
			<!-- ======================================== -->
			<div>
				<pre>Cantidad de registros: {{ rows }}</pre>
			</div>
			<div>
				<b-table :fields="fields"
						 striped
						 sortable
						 responsive
						 hover
						 :items="tabla_kc"
						 show-empty
						 :per-page="perPage"
						 :current-page="currentPage"
						 :sticky-header="false"
						 :no-border-collapse="true"
						 ref="tablaregistros"
						 id="tablaregistros"
						 :filter="filter"
						 @filtered="onFiltered">
					<template #empty="">
						<b>No hay registros para mostrar</b>
					</template>

					<template slot="cell()" slot-scope="data" style="min-width: 4em !important; margin-left: 2em !important;">
						<p @click="showinput()" v-if="kcus === true">
							<v-icon class="mr-1" style="font-size:1.2em;display:inline-block"> mdi-pencil </v-icon>
							<span style="display:inline-block;">{{ data.value }}</span>
						</p>
						<b-form-group label-for="precioref" v-else>
							<b-form-input id="precioref"
										  v-model="data.value"
										  type="number"
										  style="width:100%;display:inline-block;"
										  invalid-feedback="Complete este campo">
							</b-form-input>
							<b-button @click="GuardarPrecioRef(data)" style="background-color:green;display:inline-block;">✔</b-button>
						</b-form-group>
					</template>
					<template slot="cell(fecha)" slot-scope="data">
						{{ data.value | Date}}
					</template>

					<template #cell(selected)="{ rowSelected }">
						<template v-if="rowSelected">
							<span aria-hidden="true">&check;</span>
							<span class="sr-only">Selected</span>
						</template>
						<template v-else>
							<span aria-hidden="true">&nbsp;</span>
							<span class="sr-only">Not selected</span>
						</template>
					</template>
					<template #row-details="row">
						<b-card title="Datos del kc: ">
							<div>
								<b-list-group horizontal>
									<b-list-group class="col-3">
										<b-list-group-item><b>Id kc:</b> {{ row.item.id_kc }}</b-list-group-item>
										<b-list-group-item>
											<b>Numero de ticket:</b>
											{{ row.item.nro_ticket }}
										</b-list-group-item>
										<b-list-group-item>
											<b>Descripcion:</b>
											{{ row.item.descripcion }}
										</b-list-group-item>
										<b-list-group-item><b>Total:</b> {{ row.item.total }}</b-list-group-item>
										<b-list-group-item><b>Fecha:</b> {{ row.item.fecha }}</b-list-group-item>
									</b-list-group>
									&nbsp;
								</b-list-group>
							</div>
						</b-card>
					</template>
				</b-table>
			</div>
			<!-- ======== Tabla con los registros ======= -->
			<!-- ================ELIMINAR GASTO SALIENTE======================== -->

			<b-modal id="modal_eliminar"
					 ref="my-modal"
					 hide-footer
					 title="Eliminar"
					 ok-only>
				<div class="d-block text-center">
					<h3>
						¿Esta seguro de eliminar los datos de
						{{ infoEliminar.kc.id_kc }} - Nro ticket
						{{ infoEliminar.kc.patente }}?
					</h3>
				</div>
				<b-button class="mt-2" block @click="hideModal" title="Volver Atras">Volver Atras</b-button>
				<b-button class="mt-3"
						  variant="danger"
						  block
						  @click="deleteKc(infoEliminar.kc.id_kc)"
						  title="Eliminar">
					Eliminar
				</b-button>
			</b-modal>
			<b-container fluid>
				<b-col class="my-1">
					<b-pagination v-model="currentPage"
								  align="center"
								  pills
								  :total-rows="rows"
								  :per-page="perPage"
								  aria-controls="table_kc">
					</b-pagination>
				</b-col>
			</b-container>
			<b-modal id="modal-editar" hide-footer>
				<template #modal-title>
					<h5 class="modal-title">Editar</h5>
				</template>
				<kc-update :kc="editar" :updateTable="testFetch" />
			</b-modal>
		</div>
	</v-app>
</template>

<script>
    let api = new URL("http://45.162.168.170:8081");
	api.pathname = "kc";
	//api.port = 8081;
    import { APIControler } from "@/store/APIControler";
	import KcAlta from "./KcAlta.vue";
	import KcUpdate from "./KcUpdate.vue";

	import axios from "axios";

	export default {
        components: { KcAlta, KcUpdate },
		data() {
			return {
				tabla_kc: [],
                opcultivos: [
                    { value: null, text: "Elija un cultivo", disabled: true },
                ],
				fields: [
					{ key: "Enero", label: "Enero", sortable: true },
					{ key: "Febrero", label: "Febrero", sortable: true },
					{ key: "Marzo", label: "Marzo", sortable: true },
					{ key: "Abril", label: "Abril", sortable: true },
					{ key: "Mayo", label: "Mayo", sortable: true },
					{ key: "Junio", label: "Junio", sortable: true },
					{ key: "Julio", label: "Julio", sortable: true },
					{ key: "Agosto", label: "Agosto", sortable: true },
					{ key: "Septiembre", label: "Septiembre", sortable: true },
					{ key: "Octubre", label: "Octubre", sortable: true },
					{ key: "Noviembre", label: "Noviembre", sortable: true },
                    { key: "Diciembre", label: "Diciembre", sortable: true },
				],
				selected: [],
				periodocomplkc: [],
				meses: ['Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre'],
				kcs: [],
				filter: null,
				totalRows: 1, //Total de filas
				currentPage: 1, //Pagina actual
				perPage: 10, // Datos en la tabla por pagina
				buscar: "",
				editar: {},
                dardealta: false,
				fechainiusada: null,
				kcus: true,
                cultivousado: null,
				fechafinusada: null,
				infoEliminar: {
					id: "modal_eliminar",
					kc: -1,
				},
				btn_down_pdf: true, //Desabilito los botones, hasta que muestre los datos
				btn_del_full: true,
				msj_tabla: " Presione 'Mostrar' para ver los regitros ",
				btn_mostrar: false,
				btn_editar: false,
				btn_eliminar: false,
				btn_select: false,
				btn_limpiar: true,
			};
		},
		computed: {
			rows() {
                return (this.totalRows = this.tabla_kc.length);
			},
			rowsFilter() {
				return this.totalRows;
			},
			id() {
                return this.tabla_kc.id_kc;
			},
			sortOptions() {
				// Create an options list from our fields
				return this.fields
					.filter((f) => f.sortable)
					.map((f) => {
						return { text: f.label, value: f.key };
					});
			},
		},
		methods: {
			GuardarPrecioRef(data)
			{
				let mesazo = data.field.key
                if (this.findkcmes(mesazo) == null) {
					let kc = {
                        mes: mesazo,
						kc: data.value,
						id_cultivo: this.cultivousado,
					};
					this.postKc(kc);
				} else
				{
                    let kc = this.findkcmes2(mesazo)
					kc.kc = data.value;
                    this.putKc(kc);
				}
				console.log(data)
				this.kcus = true;
			},
            async postKc(kc) {
                let gastoSalienteAPI = new APIControler();
                gastoSalienteAPI.apiUrl.pathname = "kc/";
                this.respuesta = await gastoSalienteAPI.postData(kc);
			},
            uploadFile() {
                this.images = this.$refs.archivo.files[0];
                console.log(this.images);
            },
            async importarExcel() {
                const data = await this.images.arrayBuffer();
                let workbook = XLSX.read(data);
                var sheet_name_list = workbook.SheetNames;
                console.log('workbook1');
                console.log(workbook);
                console.log('SheetNames');
                console.log(workbook.SheetNames);
                let ProveedorAPI = new APIControler();
                let datos = XLSX.utils.sheet_to_json(workbook.Sheets[sheet_name_list[0]]);
                console.log("datos");
                console.log(datos);
                ProveedorAPI.apiUrl.pathname = "kc/"
                datos.forEach((element) => {
                    ProveedorAPI.postData(element)
                });
                this.testFetch();
            },
            async getCultivos() {
                let productosAPI = new APIControler();
                productosAPI.apiUrl.pathname = "cultivos/";
                this.listacli = await productosAPI.getData();
                let option
                this.listacli.forEach((element) => {
                    option = {};
                    option.value =
                        "http://45.162.168.170:8081/cultivos/" + element.id_cultivo + "/";
                    option.text = element.id_campo.split('/')[4] + ' - ' + element.tipo + ' - ' + element.tiposuelo;
                    this.opcultivos.push(option);
                });
            },
            async putKc(kc) {
                let respuesta = "vacio";
                await axios
                    .put(
                        "http://45.162.168.170:8081/kc/" +
                        kc.id_kc +
                        "/",
                        kc
                    )
                    .then(function (data) {
                        swal("Operación Exitosa", " ", "success");
                    })
                    .catch(function (error) {
                        const mje =
                            error.response.status < 500
                                ? "Los datos no son válidos"
                                : "Se ha detectado un problema ";
                        swal("¡ERROR!", mje, "error");
                        respuesta = error.response.data;
                    });
            },
			async testFetch() {
				try {
					const res = await fetch(api);
					const data = await res.json();
					this.tabla_kc = [];
					var lista_kc = data.results;
					let fila = {};
					this.meses.forEach((mes) =>
					{
						let kcmes = this.findkcmes(mes)
						fila[mes] = kcmes;
					})
					console.log(fila);
                    this.tabla_kc.push(fila);
				} catch (error) {
					console.log(error);
				}
			},
			editarKc(item, index) {
				this.editar = item;
			},
			//Funcion para mostrar el modal
			showModal() {
				this.$refs["my-modal"].show();
			},
			showModalinfo(item, index) {
				this.infoEliminar.kc = item;
				this.showModal();
			},
			//Funcion para esconder el modal
			hideModal() {
				this.$refs["my-modal"].hide();
			},
            fechaperiodo() {
                this.fechas = [];
				let fechaini = new Date(this.fechainiusada)
                fechaini.setDate(fechaini.getDate() + 1)
				let fechafin = new Date(this.fechafinusada)
                fechafin.setDate(fechafin.getDate() + 1)
                console.log(fechaini.getDay());
                while (fechaini.getTime() <= fechafin.getTime()) {
                    this.fechas.push(('0' + fechaini.getDate()).slice(-2) + "-" + ('0' + (fechaini.getMonth() + 1)).slice(-2) + "-" + fechaini.getFullYear());
                    fechaini.setDate(fechaini.getDate() + 1);
                }
                console.log(this.fechas);
			},
			async getKcs()
			{
                let gastoSalienteAPI = new APIControler();
                gastoSalienteAPI.apiUrl.pathname = "kc/";
                this.kcs = await gastoSalienteAPI.getData();
			},
			findkcmes(mesardo)
			{
				console.log(mesardo);
				console.log(this.cultivousado)
				let kcmes = this.kcs.find((s) => {
                    return (s.mes == mesardo && s.id_cultivo == this.cultivousado);
				})
				if (kcmes == undefined) {
					return null
				} else
				{
					console.log('devuelvo')
					return kcmes.kc;
				}
			},
            findkcmes2(mesardo) {
                let kcmes = this.kcs.find((s) => {
                    return (s.mes == mesardo && s.id_cultivo == this.cultivousado);
                })
                if (kcmes == undefined) {
                    return null
				} else {
					console.log('devuelvo')
					console.log(kcmes);
                    return kcmes;
                }
            },
            showinput() {
                this.kcus = false;
            },
            mesesperiodo() {
                this.meses = [];
                let fechaini = new Date(this.fechainiusada)
                let fechafin = new Date(this.fechafinusada)
                while (fechaini.getTime() <= fechafin.getTime()) {
					let mes;
                    let mesuco = fechaini.getMonth() + 1
					switch (mesuco) {
						case 1:
							mes = 'Enero'
							break;
						case 2:
							mes = 'Febrero'
							break;
						case 3:
							mes = 'Marzo'
							break;
						case 4:
							mes = 'Abril'
							break;
						case 5:
							mes = 'Mayo'
							break;
						case 6:
							mes = 'Junio'
							break;
						case 7:
							mes = 'Julio'
							break;
						case 8:
							mes = 'Agosto'
							break;
						case 9:
							mes = 'Septiembre'
							break;
						case 10:
							mes = 'Octubre'
							break;
						case 11:
							mes = 'Noviembre'
							break;
						case 12:
							mes = 'Diciembre'
							break;
					}
					if (!this.meses.includes(mes))
					{
                        this.meses.push(mes)
					}
                    fechaini.setDate(fechaini.getDate() + 1);
                }
                console.log(this.fechas);
			},

			altaKc() {},

			//Elimino un gasto
			async deleteKc(id_kc) {
				axios
                    .delete("https://arandadistribucion.pythonanywhere.com/kc/" + id_kc + "/")
					.then((datos) => {
						swal("Operación Exitosa", " ", "success");
						console.log(datos);
						this.hideModal();
					})
					.catch((error) => {
						swal("¡ERROR!", "Se ha detectado un problema ", "error");
						console.log(error);
						this.hideModal();
					})
					.finally(() => this.testFetch());
			},
			llenarfields(fechas)
			{
                console.log('tabla_ventas');
				console.log(fechas);
				let fields = [];
				fechas.forEach((fecha) =>
				{
					console.log('anashey')
					let option = {}
					option.key = fecha
					option.text = fecha
					console.log(option);
					fields.push(option);
				})
				console.log('god')
                console.log(fields)
				return fields;
			},
            redirect() {
                if (!this.$session.exists()) {
                    window.location.replace('/login');
                }
                if (this.$session.get('tipocuenta') == 'lector') {
                    this.dardealta = true;
                }

            },
			//Elimina todas los gastos
			async delete_all_Kc() {
				var cantidad = this.selected.length;

				try {
					for (var i = 0; i < cantidad; i++) {
						axios.delete(
							"https://arandadistribucion.pythonanywhere.com/kc/" +
								this.selected[i].id_kc +
								"/"
						);
						if (this.selected.length == 0) {
							console.log("Eliminacion Exitosa");
							break;
						}
					}
					this.hideModal();
					swal("Eliminacion Exitosa", " ", "success");
				} catch (error) {
					this.hideModal();
					swal("¡ERROR!", "Se ha detectado un problema ", "error");
					console.log(error);
				} finally {
					this.testFetch();
				}
			},

			//Funcion de busqueda
			onFiltered(filteredItems) {
				// Trigger pagination to update the number of buttons/pages due to filtering
				this.totalRows = filteredItems.length;
				this.currentPage = 1;
			},

			//-----Funciones de seleccion
			//Selecciona una a una
			seleccionar_una(items) {
				this.selected = items;
				if (this.selected.length > 0) {
					this.btn_del_full = false;
					this.btn_limpiar = false;
					if (this.selected.length > 1) {
						this.btn_mostrar = true;
						this.btn_editar = true;
						this.btn_eliminar = true;
					}
					if (this.selected.length == this.rows) {
						this.btn_select = true;
					} else {
						this.btn_select = false;
					}
				} else {
					this.btn_del_full = true;
					this.btn_mostrar = false;
					this.btn_editar = false;
					this.btn_eliminar = false;
					this.btn_limpiar = true;
				}
			},
			//Selecciona todas
			seleccionar_todas() {
				this.$refs.tablaregistros.selectAllRows();
				this.btn_del_full = false;
				this.btn_mostrar = true;
				this.btn_editar = true;
				this.btn_eliminar = true;

				this.btn_select = true;
				this.btn_limpiar = false;
			},
			//Limpia todas las selecciones
			limpiar_seleccion() {
				this.$refs.tablaregistros.clearSelected();
				this.btn_del_full = true;
				this.btn_mostrar = false;
				this.btn_editar = false;
				this.btn_eliminar = false;

				this.btn_select = false;
				this.btn_limpiar = true;
			},
			inicializar()
			{
                if (this.$session.exists()) {
                    this.cultivousado = 'http://45.162.168.170:8081/cultivos/' + String(this.$session.get('cultivused').id_cultivo) + '/';
                }
			},
		},

		beforeMount() {
            this.getCultivos();
			this.getKcs();
			this.inicializar();
			this.testFetch();
			this.redirect();
		},
	};
</script>

<style scoped>
	.myTable {
		position: absolute;
		left: 0;
		padding: 1.5em;
		margin-top: 4rem;
		overflow: auto;
		transition: 0.5s;
		width: 100%;
	}
</style>
