<template>
	<div>
		<h6>Los campos en (*) son obligatorios</h6>
		<h4>Registrar nuevo kc:</h4>

		<b-form>
			<!-- nro_ticket -->
			<b-form-group label="*Lote"
						  label-for="id_cultivo"
						  @submit.stop.prevent="handleSubmit">
				<v-autocomplete id="id_cultivo"
								v-model="kc.id_cultivo"
								:items="opcultivos"
								placeholder="Elija el cultivo"
								:state="validacion.id_cultivo.estado"
								invalid-feedback="Complete este campo"
								required
								solo
								filled>
				</v-autocomplete>
				<b-form-invalid-feedback id="id_cultivo-live-feedback">
					{{ validacion.id_cultivo.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
			<b-form-group label="*Mes"
						  label-for="mes"
						  @submit.stop.prevent="handleSubmit">
				<v-autocomplete id="mes"
								v-model="kc.mes"
								:items="opmeses"
								placeholder="Elija el mes"
								:state="validacion.mes.estado"
								invalid-feedback="Complete este campo"
								required
								solo
								filled>
				</v-autocomplete>
				<b-form-invalid-feedback id="mes-live-feedback">
					{{ validacion.mes.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
			<b-form-group label="*Kc"
						  label-for="kc"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="kc"
							  v-model="kc.kc"
							  type="number"
							  placeholder="Ingrese el numero de kc"
							  :state="validacion.kc.estado"
							  invalid-feedback="Complete este campo"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="kc-live-feedback">
					{{ validacion.kc.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
		</b-form>
		<b-button
			style="background-color: #006266;color:white;"
			variant="success"
			block
			@click="postKc()"
			>Guardar</b-button
		>
	</div>
</template>

<script>
	import { APIControler } from "@/store/APIControler";

	export default {
		props: {
			updateTable: Function,
		},
		data() {
			return {
				kc: {},
				data: {},
                opmeses: [
					{ value: "Enero", text: "1- Enero"},
					{ value: "Febrero", text: "2- Febrero"},
					{ value: "Marzo", text: "3- Marzo"},
					{ value: "Abril", text: "4- Abril"},
					{ value: "Mayo", text: "5- Mayo"},
					{ value: "Junio", text: "6- Junio"},
					{ value: "Julio", text: "7- Julio"},
					{ value: "Agosto", text: "8- Agosto"},
					{ value: "Septiembre", text: "9- Septiembre"},
					{ value: "Octubre", text: "10- Octubre"},
					{ value: "Noviembre", text: "11- Noviembre"},
					{ value: "Diciembre", text: "12- Diciembre"},
				],
                opcultivos: [
					{ value: null, text: "Elija un cultivo", disabled: true },
				],
				validacion: {
					mes: { estado: null, mensaje: "" },
                    id_cultivo: { estado: null, mensaje: "" },
                    kc: { estado: null, mensaje: "" },
				},
				respuesta: {},
			};
		},
		methods: {
			async getKc() {
                let KcAPI = new APIControler();
                this.data = await KcAPI.getData();
			},
			async postKc() {
				let gastoSalienteAPI = new APIControler();
				gastoSalienteAPI.apiUrl.pathname = "kc/";
				this.respuesta = await gastoSalienteAPI.postData(this.kc);
				this.cargarFeedback();
				this.updateTable();
			},
            async getCultivos() {
                let productosAPI = new APIControler();
                productosAPI.apiUrl.pathname = "cultivos/";
                this.listacli = await productosAPI.getData();
                let option
                this.listacli.forEach((element) => {
                    option = {};
                    option.value =
                        "http://45.162.168.170:8081/cultivos/" + element.id_cultivo + "/";
                    option.text = 'Pertenece al campo Nro ' + element.id_campo.split('/')[4] + ' - ' + element.tipo;
                    this.opcultivos.push(option);
                });
            },
			cargarFeedback() {
				let valido;
				this.respuesta=this.respuesta|| {}
				for (let key in this.validacion) {
					valido = !this.respuesta.hasOwnProperty(key);
					this.validacion[key].estado = valido;
					//console.log(key);
					if (!valido) this.validacion[key].mensaje = this.respuesta[key][0];
				}
			},
		},
        beforeMount() {
            this.getCultivos();
        },
	};
</script>

<style></style>
